.asideList {
    list-style: none;
    padding: 0;
}

.asideItem {
    display: flex;
    margin: 10px 0;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: #8585ad;
    cursor: pointer;
    letter-spacing: 1px;
    text-decoration: none;
}

.active {
    color: #f2f2f3;
}

.p {
    width: 100%;
    display: flex;
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #8585ad;
    opacity: 0.4;
    letter-spacing: 1px;
}
